<template>
  <div>
    <h1>农产品电商管理系统</h1>
    <a-menu
     theme="dark" 
     mode="inline"
     :selected-keys="[`${$route.path}`]"
     :default-open-keys="openKey"
    >
      <template v-for="item in menu">
        <template v-if="(item.meta.role && user && !user.shop) || (!item.meta.role && user && user.shop) || (item && item.name === 'Home')">
          <template v-if="item.children">
            <a-sub-menu :key="`/${item.path}`">
              <span slot="title">
                <a-icon :type="item.meta.icon" />
                <span>{{ item.meta.title }}</span>
              </span>
              <template v-for="i in item.children">
                <a-menu-item :key="`/${item.path}/${i.path}`" @click="toPage(i.name)">
                  {{i.meta.title}}
                </a-menu-item>
              </template>
            </a-sub-menu>
          </template>
          <template v-else>
            <a-menu-item :key="`/${item.path}`" @click="toPage(item.name)">
              <a-icon :type="item.meta.icon" />
              <span>{{ item.meta.title }}</span>
            </a-menu-item>
          </template>
        </template>
      </template>
    </a-menu>
  </div>
</template>
<script>
import { Menu, Icon } from 'ant-design-vue'
import menu from '@/router/menu.js'
import { mapState } from 'vuex'
export default {
  components: { AMenu: Menu, AMenuItem: Menu.Item,AIcon: Icon, ASubMenu: Menu.SubMenu },
  data() {
    return {
      menu: menu,
      prePath: process.env.VUE_APP_PRE_PATH,
      openKey: []
    }
  },
  computed: {
    ...mapState('login', ['user'])
  },
  methods: {
    toPage(name) {
      this.$router.push({
        name
      })
    },
    handleSubment() {
      const path = this.$route.path
      const key = path.substring(0, path.lastIndexOf('/'))
      this.openKey.push(key)
    }
  },
  mounted() {
    this.handleSubment()
  }
}
</script>
<style lang='scss' scoped>
h1 {
  font-size: 2em;
  color: #fff;
  overflow: hidden;
  margin: 10px 5px;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  display: -webkit-box;
  text-overflow:ellipsis;
}
</style>
