<template>
  <div class="header">
    <span v-if="user && user.shop && user.shop.logo">
      <a-avatar
        :src="`${imgPrefix}${user.shop.logo}`"
      ></a-avatar>
    </span>
    <span style="padding: 0 12px; font-size: 16px;"></span>
    <span><a-icon type="logout" @click="logout" /></span>
  </div>
</template>
<script>
import { mapActions, mapState } from 'vuex'
export default {
  data() {
    return {
      proxy: process.env.VUE_APP_API_URL,
      prefix: process.env.VUE_APP_PREFIX,
      imgPrefix: process.env.VUE_APP_IMG_PREFIX,
    }
  },
  computed: {
    ...mapState('login', ['user'])
  },
  methods: {
    ...mapActions('login', {
      getAccount: 'getAccount'
    }),
    logout() {
      sessionStorage.clear()
      this.$router.push({
        name: 'Login'
      })
      this.$store.commit('login/SET_USER', null)
    }
  },
  created() {
    if(!this.user) {
      this.getAccount(Number(sessionStorage.getItem('user_id')))
    }
  }
}
</script>
<style lang="scss" scoped>
.header {
  display: flex;
  align-content: center;
  justify-content: flex-end;
}
</style>